import React from 'react';
import { StyleSheet, View, Text, ScrollView, Button } from 'react-native';
import Colors from '../templates/Colors';
import i18n from 'i18n-js';



i18n.locale = global.language;
i18n.fallbacks = true;

const RedText = props =>{
    return(
    <Text style={{color:'red'}} >{props.children}</Text>
    );
};
const Bold = props =>{
    return(
    <Text style={{fontWeight:"bold"}} >{props.children}</Text>
    );
};


const TurnOrderScreenTH = props => {
    return (
        <ScrollView>
            <View style={styles.container}>
                <View style={styles.headerText}>
                    <Text style={{textDecorationLine:"underline"}}><Bold>Turn Order</Bold></Text>
                </View>
                <View style={styles.bodyContainer}>
                    <View>
                        <View style={styles.bodyLine}>
                            <Text style={styles.bodyTextTopic}>1.</Text>
                            <Text style={styles.bodyTextDesc}><RedText>Order</RedText></Text>
                        </View>
                        <View style={styles.bodyLine}>
                            <Text style={styles.bodyTextTopic}>2.</Text>
                            <Text style={styles.bodyTextDesc}><RedText>Order</RedText>Unit ().</Text>
                        </View>
                        <View style={styles.bodyLine}>
                            <Text style={styles.indent}></Text>
                            <Text style={styles.bodyTextTopic}>2.1</Text>
                            <Text style={styles.bodyTextDesc}><Bold>first action</Bold>(      ).</Text>
                        </View>
                        <View style={styles.bodyLine}>
                            <Text style={styles.indent}></Text>
                            <Text style={styles.bodyTextTopic}>2.2</Text>
                            <Text style={styles.bodyTextDesc}><RedText> ARO </RedText> (unit  ARO ).</Text>
                        </View>
                        <View style={styles.bodyLine}>
                            <Text style={styles.indent}></Text>
                            <Text style={styles.bodyTextTopic}>2.3</Text>
                            <Text style={styles.bodyTextDesc}><Bold>second action.</Bold></Text>
                        </View>
                        <View style={styles.bodyLine}>
                            <Text style={styles.indent}></Text>
                            <Text style={styles.bodyTextTopic}>2.4</Text>
                            <Text style={styles.bodyTextDesc}><RedText> ARO </RedText> (unit second action  first action ).</Text>
                        </View>
                        <View style={styles.bodyLine}>
                            <Text style={styles.indent}></Text>
                            <Text style={styles.bodyTextTopic}>2.5</Text>
                            <Text style={styles.bodyTextDesc}><Bold>Resolve</Bold>  <RedText></RedText>  2action  (  +/- mod step).</Text>
                        </View>
                        <View style={styles.bodyLine}>
                            <Text style={styles.bodyTextTopic}>3.</Text>
                            <Text style={styles.bodyTextDesc}>2 Order  ( <RedText>lieutenant order</RedText>).</Text>
                        </View>
                        <View style={styles.bodyLine}>
                            <Text style={styles.bodyTextTopic}>4.</Text>
                            <Text style={styles.bodyTextDesc}><Bold>End turn</Bold> ( End mission goal )</Text>
                        </View>
                    </View>
                </View>
            </View>
        </ScrollView>
    );

}

const styles = StyleSheet.create({
    headerText: {
        padding: 15,
    },
    bodyContainer: {
        paddingHorizontal: 5,
    },
    bodyLine: {
        flexDirection: "row",
        paddingBottom: 5,
    },
    bodyTextTopic: {
        width: 35,
        textAlign: "center",
        fontWeight: "bold",
        paddingRight:0,
    },
    bodyTextDesc: {
        flex: 8,
    },
    indent: {
        paddingLeft: 20
    },
    container: {
        backgroundColor: 'white',
        height:screen.height,
        
    },

});

export default TurnOrderScreenTH;